@import '/src/styles/variables';

.search-page {
  position: relative;
  min-height: 90vh;
}

.header {
  height: 200px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-family: 'Poppins', sans-serif;
  color: $darkColor;

  h1 {
    margin: 0;
  }
}

.img-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.recipes-container {
  width: 100%;
  background-color: $secondaryColor;
  padding: 1rem 0;
}

.recipe-list {
  width: 90%;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: auto;
}

@media screen and (max-width: 950px) {
  .header {
    height: auto;
    h1 {
      font-size: 1.5rem;
    }
  }

  .recipes-container {
    margin-top: 100px;
  }
}
